footer{
    width:100%;
    height: auto;
    color: white;
    display: -webkit-box;
    display: flex;
    background: #000;
    opacity: 0.9;
}
.footerDiv{
    height: 100%;
    -webkit-box-flex: 1;
            flex-grow: 1;
    text-align: center;
    padding: 20px;
    opacity: 0.9;
}
.footerDiv img{
    width:150px;
    height:150px;
    position: relative;
    top:20%;
}
.iconsContact{
    width: 100%;
    height:auto;
    text-align: center;
    display: -webkit-box;
    display: flex;
    margin-top: 30px;
}
.iconsContact span{
    position: relative;
    width: auto;
    text-align: center;
    border-right: 2px solid rgb(122, 122, 122);
    -webkit-box-flex: 1;
            flex-grow: 1;
    margin-top: 0;
    padding: 20px;
}
.iconsContact span p{
    margin-top: 10px;
}
.iconsContact span:last-child{
    border-right: none;
}

.fas{
    color: gold;
    font-size: 40px;
}
@media(max-width:992px){
    footer{
        display: inline-block;
        height: auto;
    }
    .footerDiv{
        padding:10px;
        display: block;
        height: auto;
    }
    .footerDiv img{
        width:80px;
        height:80px;
    }
    
    .iconsContact span{
        border-right: none;
        font-size: 12px;
        padding: 5px;
        border-left:1px solid white;
    }
    .iconsContact span:first-child{
        border-left: none;
    }
   
    .fas{
        font-size: 20px;
    }

}
@media(max-width:576px){
    .iconsContact{
        display: block;
    }    
    .iconsContact span {
        font-size: 12px;
    }   
    .footerDiv{
        padding:3px;
        display: block;
        height:auto;
        border: none;
        
    }
    .footerDiv img{
        width:40px;
        height:40px;
    }
    
    .iconsContact span{
        font-size: 12px;
        padding: 2px;
        -webkit-box-flex: 1;
                flex-grow: 1;
        border: none;
    }
    h4{
        font-size: 10px;
    }
   
    .fas{
        font-size: 10px;
    }

}

@media(max-width:350px){
    
    .iconsContact span {
        font-size: 12px;
        padding: 5px;
        border-left: none;
        border-bottom:1px solid rgb(107, 107, 107);
        display: block;
        width: 100%;
        height: auto;
    }
    .iconsContact span p{
        padding: 0px;
        margin: 2px;
    }
    
}
@media(max-width:200px){
    .iconsContact span{
        font-size: 10px;
    }
}

 
