
.jumbotron{
    background:url('../images/background3.jpg')fixed center !important;
    height: 400px;
    padding: 0px;
}
@media(max-width:992px){
    .jumbotron{
        height: 250px;
    }
}
@media(max-width:576px){
    .jumbotron{
        height: 200px;
    }
}
@media(max-width:200px){
    .jumbotron{
        height: auto;
    }
}
.cookie{
    width:100%;
    height:auto;
    background: rgb(51, 51, 51);
    color:rgb(211, 211, 211);
    text-align: justify;
    padding: 40px;
    top:0;
    left:0;
    position: absolute;
    opacity: 0.8;
}
.cookieBtn{
    padding:10px;
    background:rgb(0, 156, 0);
    color:rgb(255, 255, 255);
    outline:none;
    border: none;
    border-radius: 8px;
}
.cookieBtn:hover{
    background:rgb(2, 22, 2)
}

