.contactSection{
    border:1px solid rgb(255, 255, 255);
    background-image: radial-gradient(rgb(36, 36, 36),rgb(0, 0, 0));
    margin: 0 auto;
    padding:0px;
}
.contactHeader{
    width:100%;
    height:auto;
    margin-bottom: 10px;
    padding: 15px;
    text-align: center;
    font-size: 24px;
    border-bottom: 0px solid black;
    background: white;
}
.formContent{
    width:100%;
    height:auto;
    padding: 20px;
    text-align: center;
    background: rgb(233, 233, 233);
}
.mapContents{
    width:100%;
    height:auto;
    padding: 5px;
}
form{
    background: rgb(255, 255, 255);
    padding: 10px;
    
}

label{
    margin-top: 5px;
}

form>input{
    width:400px;
    padding: 20px;
    height: auto;
    outline: none;
    text-align: center;
    border:1px solid rgb(172, 172, 172);
    box-shadow: 0px 0px 10px silver;
}
textarea{
    width:600px;
    height: 180px;
    margin-top: 5px;
    outline: none;
    box-shadow: 0px 0px 3px black;
    padding: 10px;
}
form>button{
    width: 100px;
    height:50px;
    border-radius: 5px;
    box-shadow: 0px 0px 10px black;
    font-weight: 500;
}
.error{
    color: red;
}
.map{
    width:100%;
    height: 400px;
    outline: 1px solid black;
    opacity: 0.9;
}
.contactDiv{
    background:url('../images/inverso.png') no-repeat;
    height:auto;
    width:100%;
    margin-bottom: 10px;
    font-weight: 600;
    color:rgb(185, 185, 185);
    padding: 5px;
}
.adress{
    width:50%;
    height:100%;
    margin: 0 auto;
    text-align: center;
}
.contactDiv h2{
    width:100%;
    padding: 10px;
}
.contactDiv .fab{
    width: auto;
    height: auto;
    font-size: 40px;
    color:rgb(230, 230, 230);
    margin-left:20px;
    margin-top:20px;
    -webkit-transition: 0.1s;
    transition: 0.1s;
}
.contactDiv .fab:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}


@media(max-width:992px){
    .mapContents{
        width:100%;
        height:auto;
    }
    form{
        background: rgb(247, 247, 247);
        padding: 5px;
    }
    
    label{
        margin-top: 5px;
    }
    
    form>input{
        width:350px;
               
    }
    textarea{
        width:500px;
        height: 180px;
        padding: 12px;
    }
    form>button{
        width: 100px;
        height:50px;
    }
    .formContent h5{
        font-size: 15px;
    }
    .mapContents{
        width:100%;
        height:auto;
        font-size: 15px;
    }
    .map{
        width:100%;
        height: 300px;
        outline: 1px solid black;
        background: #000;
        opacity: 0.9;
    }
}
@media(max-width:768px){
    form{
        background: rgb(247, 247, 247);
        padding: 5px;
    }
    
    label{
        margin-top: 5px;
    }
    
    form>input{
        width:100%;
               
    }
    textarea{
        width:100%;
        height: 180px;
        padding: 10px;
    }
    form>button{
        width: 100px;
        height:50px;
    }
    .adress{
        width:100%;
         margin-right: 0;
         font-size: 13px;
         background: #000;
         opacity: 0.8;
    }
    .adress>p{
        opacity: 1.2;
        color:white;
    }
    .contactDiv h2{
        width:100%;
        padding: 10px;
    }
    .mapContents{
        font-size: 15px;

    }
}
@media(max-width:500px){
    .formContent{
        width:100%;
        height:auto;
        padding: 5px;
        text-align: center;
        font-size: 15px;
    }
    
    form{
        padding: 5px;
    }
    form>input{
        width:100%;
        padding: 12px;
        font-size: 12px;
               
    }
  
    textarea{
        width:100%;
        height: 120px;
        padding: 10px;
        font-size: 15px;
    }
    form>button{
        width: 80%;
        padding: 10px;
        font-size: 15px;
    }
    .adress{
        font-size: 12px;
    }
    .contactDiv h2{
        width:100%;
        padding: 5px;
        font-size: 18px;
    }
    .mapContents{
        font-size: 15px;
        
    }
    .map{
        height:250px;
    }
    .contactDiv .fab{
        font-size: 25px;
       
    }

}

@media(max-width:300px){
    .map{
        height:150px;
    }
    .adress{
        font-size: 10px;
        opacity: 1.0;
    }
}